:root {
  /* == Colors == */
  --color_black_500: #07000f;
  --color_black_400: rgba(7, 0, 15, 0.7);
  --color_black_300: rgba(7, 0, 15, 0.4);
  --color_black_200: rgba(7, 0, 15, 0.1);
  --color_black_100: rgba(2, 22, 45, 0.05);
  --color_accent_600: #085edb;
  --color_accent_500: #0067fe;
  --color_white_500: #fff;
  /* == Fonts == */
  --ff_roboto: "Roboto Condensed", sans-serif;
  --ff_arial: "Arial", sans-serif;
  /* == Decor == */
  --radius_lg: 12px;
  --radius_sm: 8px;
}

/* roboto-condensed-500 */
@font-face {
  font-display: swap;
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/roboto-condensed-v27-cyrillic_latin-500.woff2") format("woff2"), url("../fonts/roboto-condensed-v27-cyrillic_latin-500.woff") format("woff"), url("../fonts/roboto-condensed-v27-cyrillic_latin-500.ttf") format("truetype");
}
/* arial-400 */
@font-face {
  font-display: swap;
  font-family: "Arial";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/Arial-400.woff2") format("woff2"), url("../fonts/Arial-400.woff") format("woff"), url("../fonts/Arial-400.ttf") format("truetype");
}
.svg-image-icon-tick, .svg-image-icon-search, .svg-image-icon-close {
  background: url("../images/sprites/main.svg") no-repeat;
}

.svg-image-icon-close {
  background-position: 0 0;
  background-size: 200% auto;
  width: 18px;
}

.svg-image-icon-close:before {
  content: "";
  display: block;
  font-size: 0;
  line-height: 0;
  padding-top: 100%;
}

.svg-image-icon-close-dims {
  width: 18px;
  height: 18px;
}

.svg-image-icon-search {
  background-position: 100% 0;
  background-size: 200% auto;
  width: 18px;
}

.svg-image-icon-search:before {
  content: "";
  display: block;
  font-size: 0;
  line-height: 0;
  padding-top: 100%;
}

.svg-image-icon-search-dims {
  width: 18px;
  height: 18px;
}

.svg-image-icon-tick {
  background-position: 0 100%;
  background-size: 257.1428571429% auto;
  width: 14px;
}

.svg-image-icon-tick:before {
  content: "";
  display: block;
  font-size: 0;
  line-height: 0;
  padding-top: 100%;
}

.svg-image-icon-tick-dims {
  width: 14px;
  height: 14px;
}

.image-by-height {
  width: auto;
}

.image-by-height:before {
  content: none;
}

.image-by-height > svg {
  height: inherit;
}

.image-by-height > img {
  height: inherit !important;
  vertical-align: top !important;
  margin: 0 !important;
  padding: 0 !important;
  max-width: none !important;
  max-height: none !important;
  border: 0 !important;
  opacity: 0 !important;
}

/*===== global styles =====*/
* {
  box-sizing: border-box;
}

body {
  width: 100%;
  height: 100%;
  min-height: 100vh;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fff;
}

.container {
  width: 100%;
  max-width: 1920px;
  min-width: 320px;
  margin: 0 auto;
  padding: 0 20px;
}

header {
  padding: 20px;
}

.main {
  padding-top: 40px;
}

/* filter styles */
.filter {
  position: relative;
  width: 320px;
}
.filter *,
.filter ::before,
.filter ::after {
  box-sizing: border-box;
}
.filter * {
  margin: 0;
  padding: 0;
}
.filter ol,
.filter ul,
.filter li {
  list-style: none;
}
.filter button,
.filter input {
  appearance: none;
  outline: none;
}
.filter :active,
.filter :hover,
.filter :focus {
  outline: 0;
  outline-offset: 0;
}

.filter__title {
  margin-bottom: 4px;
  font-family: var(--ff_arial, "Arial", sans-serif);
  font-size: 14px;
  font-weight: 400;
  line-height: 1.4;
  letter-spacing: -0.14px;
  color: var(--color_black_500, #07000f);
}

/* filter__btn styles */
.fbutton {
  width: 100%;
  min-height: 48px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 0 6px;
  padding: 7px 7px 8px 15px;
  font-family: var(--ff_roboto, "Roboto Condensed", sans-serif);
  font-size: 16px;
  font-weight: 500;
  line-height: 1;
  letter-spacing: -0.32px;
  text-transform: uppercase;
  color: var(--color_black_500, #07000f);
  background-color: var(--color_black_100, rgba(2, 22, 45, 0.05));
  border-radius: var(--radius_lg, 12px);
  cursor: pointer;
  border: 1px solid var(--color_black_200, rgba(7, 0, 15, 0.1));
  transition: border-color 0.2s ease-out;
}
@media (hover: hover) {
  .fbutton:hover {
    border-color: var(--color_black_300, rgba(7, 0, 15, 0.4));
  }
}
.fbutton.active {
  border-color: var(--color_black_300, rgba(7, 0, 15, 0.4));
}
.fbutton .icon,
.fbutton .fbutton__amount {
  flex-shrink: 0;
}
.fbutton .fbutton__name {
  flex-grow: 1;
}

.fbutton__name {
  text-align: left;
}

.fbutton__amount {
  position: relative;
  width: 32px;
  height: 32px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: var(--color_white_500, #fff);
  border-radius: 50%;
  background-color: var(--color_accent_500, #0067fe);
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.2s ease-out, opacity 0.2s ease-out;
}
.fbutton__amount::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
  pointer-events: none;
  width: 100%;
  height: 100%;
  background-color: transparent;
  border: 1px solid var(--color_black_200, rgba(7, 0, 15, 0.1));
  border-radius: 50%;
}
.fbutton.checked .fbutton__amount {
  visibility: visible;
  opacity: 1;
}

/* filter__dropdown styles */
.filter__dropdown {
  position: absolute;
  top: calc(100% + 8px);
  left: 0;
  width: 100%;
  background-color: var(--color_white_500, #fff);
  box-shadow: 0px 4px 8px 0px rgba(0, 0, 0, 0.02), 0px 4px 32px 0px rgba(0, 0, 0, 0.02);
  border-radius: var(--radius_lg, 12px);
  border: 1px solid var(--color_black_200, rgba(7, 0, 15, 0.1));
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transition: opacity 0.2s ease-out, visibility 0.2s ease-out;
}
.filter__dropdown.opened {
  opacity: 1;
  visibility: visible;
  pointer-events: all;
}

/* filter__tabs styles */
.ftabs {
  position: relative;
}

.ftabs__wrapper {
  overflow-x: auto;
  scrollbar-width: none;
  padding-bottom: 1px;
}
.ftabs__wrapper::-webkit-scrollbar {
  -webkit-appearance: none;
  display: none;
  width: 0;
  height: 0;
}
.ftabs__wrapper::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 1px;
  background-color: var(--color_black_200, rgba(7, 0, 15, 0.1));
}

.ftabs__list {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 0 16px;
  padding: 0 15px;
}
.ftabs__list::after {
  content: "|";
  display: block;
  width: 15px;
  height: 100%;
  opacity: 0;
  pointer-events: none;
}

.ftabs__btn {
  position: relative;
  padding: 11px 0;
  font-family: var(--ff_roboto, "Roboto Condensed", sans-serif);
  font-size: 16px;
  font-weight: 500;
  line-height: 1;
  letter-spacing: -0.32px;
  text-transform: uppercase;
  color: var(--color_black_300, rgba(7, 0, 15, 0.4));
  background-color: transparent;
  border: none;
  cursor: pointer;
  transition: color 0.2s ease-out;
}
@media (hover: hover) {
  .ftabs__btn:hover {
    color: var(--color_black_500, #07000f);
  }
}
.ftabs__btn.active {
  color: var(--color_black_500, #07000f);
}
.ftabs__btn.active::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: -1px;
  width: 100%;
  min-height: 1px;
  background-color: var(--color_black_500, #07000f);
  transition: min-height 0.2s ease-out;
}
@media (hover: hover) {
  .ftabs__btn.active:hover::after {
    min-height: 2px;
  }
}

/* filter__tabs styles */
.flabels {
  padding: 11px 0;
  overflow-x: auto;
  scrollbar-width: none;
  border-bottom: 1px solid var(--color_black_200, rgba(7, 0, 15, 0.1));
}
.flabels::-webkit-scrollbar {
  -webkit-appearance: none;
  display: none;
  width: 0;
  height: 0;
}
.flabels.empty {
  display: none;
}

.flabels__list {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 0 4px;
  padding: 0 15px;
}
.flabels__list::after {
  content: "-";
  display: block;
  width: 15px;
  height: 100%;
  opacity: 0;
  pointer-events: none;
}

.flabels__btn {
  padding: 10px 11px;
  font-family: var(--ff_roboto, "Roboto Condensed", sans-serif);
  font-size: 14px;
  font-weight: 500;
  line-height: 1.2;
  letter-spacing: -0.28px;
  text-transform: uppercase;
  white-space: nowrap;
  color: var(--color_black_500, #07000f);
  background: var(--color_black_100, rgba(2, 22, 45, 0.05));
  border-radius: var(--radius_sm, 8px);
  border: 1px solid var(--color_black_200, rgba(7, 0, 15, 0.1));
  cursor: pointer;
  transition: border-color 0.2s ease-out;
}
@media (hover: hover) {
  .flabels__btn:hover {
    border-color: var(--color_black_300, rgba(7, 0, 15, 0.4));
  }
}

.flabels__btn--cross {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 0 4px;
}
.flabels__btn--cross::after {
  content: "";
  display: block;
  width: 16px;
  height: 16px;
  background: url(../images/sprites/main.svg) no-repeat;
  background-position: 0 0;
  background-size: 200% auto;
}

/* filter__items styles */
.filter__box {
  overflow: hidden;
}

.filter__items-container {
  position: relative;
  padding: 11px 4px;
}
.filter__items-container::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  z-index: 2;
  width: 100%;
  height: 80px;
  background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, #fff 100%);
  border-radius: 0 0 12px 12px;
  pointer-events: none;
  transition: opacity 0.2s ease-out;
}
.filter__items-container.scrolled::after {
  opacity: 0;
}

.fitems {
  position: relative;
  display: none;
  min-height: 48px;
  max-height: 248px;
  height: 100%;
  overflow-y: auto;
  scrollbar-width: thin;
  scrollbar-color: var(--color_black_500, #07000f) transparent;
}
.fitems.active {
  display: block;
}
.fitems::-webkit-scrollbar {
  background-color: transparent;
  width: 4px;
}
.fitems::-webkit-scrollbar-track * {
  background-color: transparent;
}
.fitems::-webkit-scrollbar-thumb {
  background-color: var(--color_black_500, #07000f);
  border-radius: 4px;
  border: transparent;
}

.fitems__input {
  position: absolute;
  clip: rect(1px, 1px, 1px, 1px);
  clip-path: inset(0 0 99.9% 99.9%);
  overflow: hidden;
  height: 1px;
  width: 1px;
  padding: 0;
  border: 0;
}
.fitems__input:checked + .fitems__label {
  background-color: var(--color_white_500, #fff);
}
.fitems__input:checked + .fitems__label .fitems__checkbox {
  background-color: var(--color_accent_500, #0067fe);
}
.fitems__input:checked + .fitems__label .fitems__checkbox::before {
  visibility: visible;
  opacity: 1;
}
@media (hover: hover) {
  .fitems__input:checked + .fitems__label:hover .fitems__checkbox {
    background-color: var(--color_accent_600, #085edb);
  }
}
.fitems__input:disabled + .fitems__label {
  background-color: var(--color_white_500, #fff);
  opacity: 0.7;
  cursor: default;
}
.fitems__input:disabled:checked + .fitems__label .fitems__checkbox {
  background-color: var(--color_accent_500, #0067fe);
}

.fitems__label {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 8px 11px;
  cursor: pointer;
  transition: background-color 0.2s ease-out;
}
@media (hover: hover) {
  .fitems__label:hover {
    background-color: var(--color_black_100, rgba(2, 22, 45, 0.05));
  }
}

.fitems__title {
  font-family: var(--ff_arial, "Arial", sans-serif);
  font-size: 16px;
  font-weight: 400;
  line-height: 1.5;
  color: var(--color_black_500, #07000f);
}

.fitems__checkbox {
  position: relative;
  width: 32px;
  height: 32px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: var(--color_white_500, #fff);
  border-radius: 50%;
  background-color: transparent;
}
.fitems__checkbox::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
  pointer-events: none;
  width: 100%;
  height: 100%;
  background-color: transparent;
  border: 1px solid var(--color_black_200, rgba(7, 0, 15, 0.1));
  border-radius: 50%;
}
.fitems__checkbox::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 14px;
  height: 14px;
  background: url(../images/sprites/main.svg) no-repeat;
  background-position: 0 100%;
  background-size: 257.1428571429% auto;
  opacity: 0;
  visibility: hidden;
}

footer {
  padding: 20px;
}