/*===== global styles =====*/
* {
  box-sizing: border-box;
}

// min-height: 0.001vw; // — Fixes Safari bug with viewport units in calc()

body {
  width: 100%;
  height: 100%;
  min-height: 100vh;
  // display: flex;
  // flex-direction: column;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fff;
}

